.homeContainer{
    height: 100%;
    .navactive{
        color:#fff !important;
    }
}

.userinfo{
    padding:70px 0 60px 0;
    dt{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 auto;
        img{
            width: 100%;
            height: 100%;
        }
    }
    dd{
        padding-top:15px;
        color:#fff;
        text-align: center;
    }
}

.navtop{
    background: #fff;
    border-bottom: 1px solid #eee;
    height: 60px;
    line-height: 60px;
    p{
        display: inline-block;
        padding: 0 20px 0 5px;
        position: relative;
        margin: 0 4px;
        a{
            color:#333;
            &.active{
                color:#229FFF;
            }
        }
        span{
            position: absolute;
            top:50%;
            right: 2px;
            width: 10px;
            height: 10px;
            background: #ccc;
            color:#fff;
            text-align: center;
            line-height: 10px;
            transform: translateY(-50%);
            cursor: pointer;
        }
    }
}


